<template>
  <section style="margin-left:15px;margin-right:15px;margin-top: 15px;">
    <el-divider></el-divider>
    <el-descriptions class="margin-top" title="" :column="3" :size="size">
      <template slot="title">
        <span>{{ modelrun.info.run_id }}</span>
      </template>
      <template slot="extra">
        <router-link v-if="firstID === 'all'"
          :to="{ name: '模型实验跟踪', params: { experiment_id: modelrun.info.experiment_id } }"
          style="text-decoration: none;color: aliceblue;">
          <el-button class="return-list"><i class="el-icon-arrow-left"
              style="margin-right: 5px"></i>返回模型Run列表</el-button>
        </router-link>
      </template>
      <el-descriptions-item label="开始时间">
        <timeago :datetime="modelrun.info.start_time" locale="zh-CN"></timeago>
      </el-descriptions-item>
      <el-descriptions-item label="实验编号">
        {{ modelrun.info.experiment_id }}
      </el-descriptions-item>
      <el-descriptions-item label="Lifecycle Stage">
        {{ modelrun.info.lifecycle_stage }}
      </el-descriptions-item>
      <el-descriptions-item label="工件路径">
        {{ modelrun.info.artifact_uri }}
      </el-descriptions-item>
      <el-descriptions-item label="标签">
        {{ modelrun.data.tags[0] || '暂无' }}
      </el-descriptions-item>
    </el-descriptions>

    <el-collapse v-model="activeNames" @change="handleChange">
      <el-collapse-item title="模型实验描述" name="1">
        <span v-if="descvisalble && modelrun.data.tags.length > 1 && modelrun.data.tags[1].key === 'mlops.framework'">{{
          modelrun.data.tags[1]["value"]
        }}&nbsp;&nbsp;</span>
        <span
          v-else-if="descvisalble && modelrun.data.tags.length > 0 && modelrun.data.tags[0].key === 'mlops.framework'">{{
            modelrun.data.tags[0]["value"]
          }}&nbsp;&nbsp;</span>
        <span v-else v-show="descvisalble">暂无&nbsp;&nbsp;</span>
        <el-button v-if="descvisalble" type="text" @click="handleEdit"><i class="el-icon-edit"></i></el-button>

        <el-input v-show="descEditable" type="textarea" style="width:60%" :rows="3"
          v-model="editModelRunMeta.description"></el-input>
        <div>
          <el-button type="text" v-show="descEditable" @click="handelCancel" style="margin-right: 0px;"
            icon="el-icon-arrow-left">取消</el-button>
          <el-button style="align:right" v-show="descEditable" type="text" icon="el-icon-check" @click="submitSave"
            circle>保存</el-button>
        </div>
      </el-collapse-item>
      <el-collapse-item :title="parameters" name="2">
        <!--列表-->
        <el-table :data="modelrun.data.params" highlight-current-row v-loading="listLoading"
          @selection-change="selsChange" style="width: 50%;">
          <el-table-column prop="key" label="Name" min-width="55%">
            <template slot-scope="scope">
              <img src="@/assets/icon-yes.svg" />
              <span>
                {{ scope.row.key }}
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="value" label="Value" min-width="45%">
          </el-table-column>
        </el-table>
      </el-collapse-item>
      <el-collapse-item :title="metrics" name="3">
        <!--列表-->
        <el-table :data="modelrun.data.metrics" highlight-current-row v-loading="listLoading"
          @selection-change="selsChange" style="width: 50%;">
          <el-table-column prop="key" label="Name" min-width="55%">
            <template slot-scope="scope">
              <img src="@/assets/icon-yes.svg" />
              <span>
                {{ scope.row.key }}
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="value" label="Value" min-width="45%">
          </el-table-column>
        </el-table>
      </el-collapse-item>
    </el-collapse>
    <br>
    <br>
  </section>
</template>

<script>
// {{ regedExperiment.creation_timestamp | formatDate}}
import { getModelRun, EditModelRunDesc } from '@/api/api'
import moment from 'moment'
export default {
  data () {
    return {
      parameters: 'Parameters',
      Metrics: 'Metrics',
      modelrun: {},
      // edit_btn: true,
      descvisalble: true,
      descEditable: false,
      editModelRunMeta: {
        description: ''
      },
      activeNames: ['1', '2'],
      firstID: 'all',
      altListData: [],
      moment: moment,
      listLoading: false,
      filters: {
        name: ''
      },
      total: 0,
      page: 1,
      sels: [] // 列表选中列
    }
  },
  filters: {
    formatDate: function (value) {
      let date = new Date(value)
      let y = date.getFullYear()
      let MM = date.getMonth() + 1
      MM = MM < 10 ? ('0' + MM) : MM
      let d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      let h = date.getHours()
      h = h < 10 ? ('0' + h) : h
      let m = date.getMinutes()
      m = m < 10 ? ('0' + m) : m
      let s = date.getSeconds()
      s = s < 10 ? ('0' + s) : s
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s
    }
  },
  watch: {
  },
  methods: {
    // 获取模型版本信息
    getRun () {
      let self = this
      let params = {
        user_name: JSON.parse(sessionStorage.getItem('name')),
        run_id: this.$route.params.run_id
      }
      console.log(params, 'paraar')
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getModelRun(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.modelrun = data
          if (data.data.params) {
            if (data.data.params.length > 0) {
              self.parameters = 'Parameters (' + data.data.params.length + ')'
            }
          } else {
            self.parameters = 'Parameters (0)'
          }
          if (data.data.metrics) {
            if (data.data.metrics.length > 0) {
              self.metrics = 'Metrics (' + data.data.metrics.length + ')'
            }
          } else {
            self.metrics = 'Metrics (0)'
          }

          console.log(self.modelrun, 'self.modelrun')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 提交模型描述编辑
    submitSave: function () {
      let self = this
      // NProgress.start();
      let params = JSON.stringify({
        run_id: self.$route.params.run_id,
        description: self.editModelRunMeta.description,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      })
      console.log(params, 'params')
      let header = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      EditModelRunDesc(header, params).then(_data => {
        let { msg, code } = _data
        if (code === '999999') {
          self.$message({
            message: '添加成功',
            center: true,
            type: 'success'
          })
          this.descEditable = false
          this.descvisalble = true
          this.editModelRunMeta.description = ''
          self.getRun()
        } else if (code === '999997') {
          self.$message.error({
            message: msg,
            center: true
          })
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
          this.editModelRunMeta.description = ''
          this.descEditable = false
          this.descvisalble = true
          self.getRun()
        }
      })
    },
    handelCancel: function () {
      this.descEditable = false
      this.descvisalble = true
      this.editModelRunMeta.description = ''
    },
    handleEdit: function () {
      this.descEditable = true
      this.descvisalble = false
      if (this.modelrun.data.tags.length > 0) {
        this.editModelRunMeta.description = this.modelrun.data.tags[0].key + ':' + this.modelrun.data.tags[0].value
      } else {
        this.editModelRunMeta.description = '暂无'
      }
    },
    handleChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      this.page = val
    }
  },
  mounted () {
    this.getRun()
    // this.getModelVersionsInfo(this.isall)
    // if (this.$route.params.firstID) {
    //   this.firstID = this.$route.params.firstID
    // } else {
    //   this.firstID = 'all'
    //  }
    // this.firstID = this.$route.params.firstGroup
  }
}
</script>

